@import "https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Titan+One&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.list {
  margin: 0;
  list-style: none;
}

.link {
  text-decoration: none;
}

body, h1, h2, h3, ul, ol, p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

* {
  outline: none;
}

button {
  border: none;
  margin: 0;
  padding: 0;
}

:root {
  --main-text-color: #000;
  --secondary-text-color: #907e82;
  --light-text-color: #fff;
  --accent-color: #d41443;
  --main-bg-color: #fff;
  --secondary-bg-color: #fff5f6;
  --background-color: #f4c9d3;
  --round-color: #ffa5ba;
  --timing-animation: cubic-bezier(.4, 0, .2, 1);
}

.hidden {
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  height: 1px;
  white-space: nowrap;
  width: 1px;
  position: absolute;
  overflow: hidden;
}

.prevent-scroll {
  overflow: hidden;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 16px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

.header {
  width: 100%;
  z-index: 5;
  margin-top: 8px;
  font-weight: 700;
  position: absolute;
  left: 0;
}

@media screen and (min-width: 768px) {
  .header {
    margin-top: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .header {
    margin-top: 37px;
  }
}

.header .container {
  align-items: center;
  margin: 0 auto;
  display: flex;
}

@media screen and (min-width: 1200px) {
  .header__logo {
    margin-right: 128px;
  }
}

.header__nav {
  display: none;
}

@media screen and (min-width: 1200px) {
  .header__nav {
    display: block;
  }
}

.header__list {
  gap: 35px;
  display: flex;
}

.header__item {
  transition: all 250ms var(--timing-animation);
}

.header__item:focus, .header__item:hover {
  transition: all 250ms var(--timing-animation);
  transform: scale(1.02);
}

.header__link {
  color: var(--light-text-color);
  transition: all 250ms var(--timing-animation);
  font-size: 16px;
  line-height: 1.3125;
}

.header__link:hover, .header__link:focus {
  color: var(--accent-color);
  transition: all 250ms var(--timing-animation);
}

.header__link--accent {
  color: var(--accent-color);
}

.header-menu__button {
  width: 20px;
  height: 12px;
  cursor: pointer;
  transition: all var(--timing-animation) 1000ms;
  background-color: rgba(0, 0, 0, 0);
  background-image: url("Burger.0b3b7bba.svg");
  background-repeat: no-repeat;
  background-size: contain;
  margin-left: auto;
  display: block;
}

@media screen and (min-width: 1200px) {
  .header-menu__button {
    display: none;
  }
}

.modal-button-header {
  width: 210px;
  height: 44px;
  text-align: center;
  color: var(--accent-color);
  background-color: var(--main-bg-color);
  cursor: pointer;
  transition: all 250ms var(--timing-animation);
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding-left: 37px;
  padding-right: 37px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.3125;
  display: flex;
}

.modal-button-header:hover, .modal-button-header:focus {
  transition: all 250ms var(--timing-animation);
  background-color: #ecebeb;
  transform: scale(1.02);
}

@media screen and (min-width: 768px) {
  .modal-button-header {
    width: 160px;
    margin-left: 32px;
    display: flex;
  }
}

.modal-button-header:after {
  content: "";
  width: 4px;
  height: 8px;
  background-image: url("continiue.8cc95434.svg");
  background-repeat: no-repeat;
  background-size: contain;
  margin-left: 12px;
  transform: translateY(2px);
}

.modal-open-mob {
  width: 210px;
  height: 44px;
  text-align: center;
  color: var(--accent-color);
  background-color: var(--main-bg-color);
  transition: all 250ms var(--timing-animation);
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding-left: 37px;
  padding-right: 37px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.3125;
  display: flex;
}

@media screen and (min-width: 768px) {
  .modal-open-mob {
    width: 186px;
  }
}

.modal-open-mob:hover, .modal-open-mob:focus {
  transition: all 250ms var(--timing-animation);
  background-color: #ecebeb;
  transform: scale(1.02);
}

.modal-open-mob:after {
  content: "";
  width: 5.5px;
  height: 8px;
  background-image: url("continiue.8cc95434.svg");
  background-repeat: no-repeat;
  background-size: contain;
  margin-left: 16px;
  transform: translateY(2px);
}

@media screen and (min-width: 768px) {
  .modal-open-mob:after {
    width: 4.5px;
    margin-left: 14px;
  }
}

.hide-mob {
  display: none;
}

@media screen and (min-width: 768px) {
  .hide-mob {
    display: block;
  }
}

@media screen and (min-width: 1200px) {
  .hide-mob {
    margin-left: auto;
  }
}

.mob-menu-container {
  max-width: 320px;
  height: 100%;
  background-color: none;
  margin-top: 60px;
  margin-left: auto;
  margin-right: auto;
}

.mob-menu {
  width: 100vw;
  height: 100vh;
  z-index: 5;
  pointer-events: all;
  visibility: visible;
  opacity: 1;
  transition: transform 1000ms var(--timing-animation), translate 1000ms var(--timing-animation);
  background-color: #e17992;
  font-weight: 700;
  position: fixed;
  top: 0;
  right: 0;
}

@media screen and (min-width: 768px) {
  .mob-menu {
    width: 254px;
    right: 0;
  }
}

@media screen and (min-width: 1200px) {
  .mob-menu {
    display: none;
  }
}

.menu-close-btn {
  width: 14px;
  height: 14px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  background-image: url("close-menu.2c7d6c9b.svg");
  background-repeat: no-repeat;
  background-size: contain;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  margin-right: 20px;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
}

.mob-menu__list {
  margin-top: 19px;
}

.mob-menu__item {
  transition: all 250ms var(--timing-animation);
  color: var(--light-text-color);
  border-bottom: 1px solid #e18298;
  padding: 11px 0 11px 20px;
}

.mob-menu__item:first-child {
  border-top: 1px solid #e18298;
}

.mob-menu__item:focus, .mob-menu__item:hover {
  color: var(--accent-color);
  transition: all 250ms var(--timing-animation);
  transition: all 250ms var(--timing-animation);
  transform: scale(1.02);
}

.mob-menu__link {
  color: inherit;
  width: inherit;
  font-size: 14px;
  line-height: 1.28571;
}

.mob-menu__link--accent {
  color: var(--accent-color);
}

.mob-menu.mob-menu--hide {
  transition: all 1000ms var(--timing-animation);
  pointer-events: none;
  visibility: hidden;
  transform: translateX(200%);
}

.cow-feature {
  width: 300px;
  height: 300px;
  z-index: 6;
  background-image: url("cow-balloon-min.12c8c5c3.png");
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  top: 30%;
  left: -100%;
}

@media screen and (min-width: 768px) {
  .cow-feature {
    display: none;
  }
}

.backdrop-buynow {
  width: 100%;
  height: 100%;
  opacity: 1;
  transition: opacity 250ms var(--timing-animation);
  z-index: 998;
  background-color: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop-buynow.hidden {
  transition: opacity 250ms var(--timing-animation);
  opacity: 0;
  pointer-events: none;
}

.modal-buynow {
  min-width: 320px;
  height: 100%;
  background-image: url("about-bgmilk-desk.589cd0ab.png");
  background-color: var(--secondary-bg-color);
  visibility: visible;
  transition: transform 250ms var(--timing-animation), opacity 250ms var(--timing-animation), scale 250ms var(--timing-animation);
  border-radius: 24px;
  padding: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-x: hidden;
  overflow-y: auto;
  transform: translate(-50%, -50%)scale(1);
}

.backdrop-buynow.hidden .modal-buynow {
  transition: transform 250ms var(--timing-animation), opacity 250ms var(--timing-animation), scale 250ms var(--timing-animation);
  opacity: 0;
  visibility: hidden;
  transform: translate(-70%, -70%)scale(.25);
}

@media screen and (min-width: 720px) {
  .modal-buynow {
    max-height: 800px;
  }
}

.close-modal-button-buynow {
  width: 30px;
  height: 30px;
  border: 1px solid var(--accent-color);
  transform: rotate(-100);
  background-color: var(--accent-color);
  transition: 250ms transform var(--timing-animation);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 8px;
  right: 15px;
}

.close-modal-button-buynow:hover, .close-modal-button-buynow:focus {
  transform: rotate(100);
}

.link-icon-close {
  width: 16px;
  height: 16px;
  transition: 250ms transform var(--timing-animation);
  display: flex;
  transform: rotate(0deg);
}

.link-icon-close:hover, .link-icon-close:focus {
  transform: rotate(180deg);
}

.modal-title-buynow {
  text-align: center;
  letter-spacing: .03em;
  color: var(--accent-color);
  font-family: Titan One;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.5;
}

@media screen and (min-width: 1200px) {
  .modal-title-buynow {
    line-height: 1.15;
  }
}

.product-check {
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  display: flex;
}

.ice-check {
  color: var(--secondary-text-color);
  transition: 250ms fill var(--timing-animation);
  flex-direction: column;
  align-items: center;
  display: flex;
  position: relative;
}

.ice-check:hover .checkborder {
  opacity: 1;
}

@media screen and (max-width: 720px) {
  .ice-check {
    font-size: smaller;
  }
}

.checkborder {
  border: 3px solid var(--round-color);
  width: 90px;
  height: 130px;
  z-index: 999;
  opacity: 0;
  border-radius: 10%;
  display: block;
  position: absolute;
  top: -5px;
}

@media screen and (max-width: 680px) {
  .checkborder {
    width: 75px;
    height: 110px;
    top: -8px;
  }
}

.check:checked + .checkborder {
  opacity: 1;
}

.delivery {
  background-color: #fff;
  margin: 30px 0;
  padding: 10px;
  position: relative;
  box-shadow: 0 0 20px 19px #fff;
}

.delivery-list {
  margin-left: 0;
  margin-right: 30px;
}

.delivery-menu {
  align-items: center;
  display: flex;
}

.delivery-menu:not(:last-child) {
  margin-bottom: 10px;
}

.cow, .courier, .helicopter {
  color: var(--round-color);
}

.cow:hover, .cow:focus, .courier:hover, .courier:focus, .helicopter:hover, .helicopter:focus {
  color: var(--accent-color);
}

.cow-pic, .courier-pic, .helicopter-pic {
  width: 30%;
  transition: transform 250ms var(--timing-animation);
  position: absolute;
  top: 10px;
  left: 200px;
}

.cow:hover:before {
  content: "";
  height: 120%;
  width: 100%;
  background-image: url("cow.14ce1bbc.jpg");
  background-repeat: no-repeat;
  display: block;
  position: absolute;
  top: 0;
  left: 150px;
}

@media screen and (min-width: 720px) {
  .cow:hover:before {
    left: 200px;
  }
}

.courier:hover:before {
  content: "";
  height: 130%;
  width: 100%;
  background-image: url("courier.d60264b2.jpg");
  background-repeat: no-repeat;
  display: block;
  position: absolute;
  top: 0;
  left: 150px;
}

@media screen and (min-width: 720px) {
  .courier:hover:before {
    left: 200px;
  }
}

.helicopter:hover:before {
  content: "";
  height: 120%;
  width: 100%;
  background-image: url("helicopter.cbfee4f3.jpg");
  background-repeat: no-repeat;
  display: block;
  position: absolute;
  top: 0;
  left: 150px;
}

@media screen and (min-width: 720px) {
  .helicopter:hover:before {
    left: 200px;
  }
}

.radio_input {
  width: 0;
  height: 0;
  opacity: 0;
  position: absolute;
}

.custom-radio-btn {
  width: 28px;
  height: 28px;
  vertical-align: text-top;
  background: #fff;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin-right: 5px;
  display: inline-block;
  position: relative;
}

.custom-radio-btn:before {
  content: "";
  width: 25px;
  height: 25px;
  background-image: url("header-logo-pic.0bfb7532.png");
  background-repeat: no-repeat;
  border-radius: 50%;
  transition: all .2s ease-in;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(0);
}

.radio_input:checked + .custom-radio-btn:before {
  transform: translate(-50%, -50%)scale(1);
}

.modal-form-buynow {
  width: 100%;
  margin-top: 12px;
}

.modal-input {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  transition: 250ms border-color var(--timing-animation);
  letter-spacing: .01em;
  color: var(--secondary-text-color);
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  outline: rgba(0, 0, 0, 0);
  padding-left: 10px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.16667;
}

.modal-input:hover, .modal-input:focus {
  border-color: var(--accent-color);
  fill: var(--accent-color);
}

.modal-wrap {
  position: relative;
}

.modal-field {
  margin-bottom: 10px;
}

.form-icon {
  transition: 250ms fill var(--timing-animation);
  position: absolute;
  top: 50%;
  left: 12px;
  transform: translateY(-50%);
}

.modal-input:hover + .form-icon, .modal-input:focus + .form-icon {
  fill: var(--accent-color);
}

.modal-input-comment {
  box-sizing: border-box;
  width: 100%;
  height: 120px;
  letter-spacing: .01em;
  color: rgba(117, 117, 117, .5);
  transition: 250ms border-color var(--timing-animation);
  resize: none;
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  outline: rgba(0, 0, 0, 0);
  margin-bottom: 10px;
  padding: 12px 16px;
  font-size: 14px;
  line-height: 1.14286;
  top: 518px;
  left: 576px;
}

.modal-input-comment::-ms-input-placeholder {
  letter-spacing: .01em;
  color: rgba(117, 117, 117, .5);
  font-size: 12px;
  font-weight: 400;
  line-height: 1.16667;
  position: absolute;
}

.modal-input-comment::placeholder {
  letter-spacing: .01em;
  color: rgba(117, 117, 117, .5);
  font-size: 12px;
  font-weight: 400;
  line-height: 1.16667;
  position: absolute;
}

.modal-input-comment:hover, .modal-input-comment:focus {
  border-color: var(--accent-color);
  fill: var(--accent-color);
}

.modal-button {
  letter-spacing: .06em;
  color: #fff;
  width: 200px;
  height: 50px;
  background-color: var(--background-color);
  cursor: pointer;
  text-align: center;
  transition: width 250ms var(--timing-animation), height 250ms var(--timing-animation);
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 4px;
  margin: auto;
  padding: 10px 32px;
  font-family: Titan One;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.875;
  display: block;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.modal-button:hover {
  width: 203px;
  height: 51px;
  color: var(--accent-color);
}

.modal-label {
  letter-spacing: .01em;
  color: var(--secondary-text-color);
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.16667;
  display: block;
}

.hero {
  background-color: #ffb8ca;
  overflow: hidden;
}

.hero-container {
  padding-top: 194px;
  padding-bottom: 158px;
}

@media screen and (min-width: 768px) {
  .hero-container {
    padding-top: 102px;
    padding-bottom: 190px;
  }
}

@media screen and (min-width: 1200px) {
  .hero-container {
    padding-top: 147px;
    padding-bottom: 316px;
  }
}

.hero .container {
  position: relative;
}

.hero-title {
  width: 218px;
  z-index: 2;
  position: relative;
}

@media screen and (min-width: 768px) {
  .hero-title {
    width: 162px;
  }
}

@media screen and (min-width: 1200px) {
  .hero-title {
    width: 277px;
  }
}

.hero-title__top {
  letter-spacing: .04em;
  text-transform: uppercase;
  color: #fff;
  font-family: Titan One;
  font-size: 26px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.15385;
}

@media screen and (min-width: 768px) {
  .hero-title__top {
    font-size: 22px;
    line-height: 1.13636;
  }
}

@media screen and (min-width: 1200px) {
  .hero-title__top {
    font-size: 38px;
    line-height: 1.15789;
  }
}

.hero-title__bottom {
  text-transform: uppercase;
  color: #d41443;
  font-family: Titan One;
  font-size: 48px;
  font-style: normal;
  font-weight: 400;
  line-height: 96.5%;
}

@media screen and (min-width: 768px) {
  .hero-title__bottom {
    font-size: 34px;
    line-height: .905;
  }
}

@media screen and (min-width: 1200px) {
  .hero-title__bottom {
    font-size: 58px;
  }
}

.hero-button {
  z-index: 2;
  margin-top: 20px;
  display: flex;
  position: relative;
  right: auto;
}

@media screen and (min-width: 1200px) {
  .hero-button {
    margin-top: 24px;
  }
}

.hero-button__content {
  width: 63px;
  height: 24px;
  color: #fff;
  background: #d41443;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
