.about-product__name {
  justify-content: center;
  text-align: center;
  color: var(--accent-color);
  margin-bottom: 0px;
  margin-top: 15px;
  font-size: 34px;
  font-weight: 400;
  font-family: 'Titan One';
  margin-bottom: 32px;

  @include tablet {
    font-size: 48px;
    margin-top: 16px;
    margin-bottom: 40px;
  }
  @include desktop {
    font-size: 58px;
    letter-spacing: 0.06em;
    margin-bottom: 81px;
  }
}

